@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html,body{height:100%;background-color:#f5f9fc;font-family:"Open Sans", sans-serif}.container{height:100%;display:flex;align-items:center;justify-content:center}.card{position:relative;display:flex;justify-content:center;margin:auto;margin-top:40px;cursor:pointer;width:22em;max-width:80%;padding:2em 0;background:#fff;box-shadow:0 0 6px 0 rgba(32,32,36,0.12);transition:all 0.35s ease}.card::before,.card::after{content:"";position:absolute;top:0;left:0;width:100%;background:#000;height:4px}.card::before{width:0;opacity:0;transition:opacity 0 ease, width 0 ease;transition-delay:0.5s}.card::after{width:100%;background:white;transition:width 0.5s ease}.card .content{width:18em;max-width:80%}.card .logo{margin:auto;margin-bottom:0.5em;width:10.625em;transition:all 0.35s ease}.card .txt{color:#999;font-weight:600;text-transform:uppercase;margin:0;letter-spacing:2px}.card .hover_content{overflow:hidden;max-height:0;transform:translateY(1em);transition:all 0.55s ease}.card .hover_content p{margin:1.5em 0 0;color:#6e6e70;line-height:1.4em}.card:hover{width:22em;transition:all 0.5s ease;box-shadow:0 10px 20px 0 rgba(32,32,36,0.12)}.card:hover::before{width:100%;opacity:1;transition:opacity 0.5s ease, width 0.5s ease;transition-delay:0}.card:hover::after{width:0;opacity:0;transition:width 0 ease}.card:hover .logo{margin-bottom:0.5em}.card:hover .hover_content{max-height:20em;transform:none}

#snip0015 {
  color: #fff;
  position: relative;
  overflow: hidden;
  min-width: 220px;
  max-width: 400px;
  max-height: 300px;
  width: 100%;
  margin: 20px;
  background: #000000;
  text-align: center;
}
#snip0015 * {
  box-sizing: border-box;
}
#snip0015 img {
  opacity: 1;
  width: 100%;
  transition: opacity 0.35s;
}
#snip0015 #figcaption {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 3em 3em;
  width: 100%;
  height: 100%;
}
#snip0015 #figcaption::before {
  position: absolute;
  top: 50%;
  right: 30px;
  bottom: 50%;
  left: 30px;
  border-top: 1px solid rgba(255, 255, 255, 0.8);
  border-bottom: 1px solid rgba(255, 255, 255, 0.8);
  content: "";
  opacity: 0;
  background-color: #ffffff;
  transition: all 0.4s;
  transition-delay: 0.6s;
}
#snip0015 h2,
#snip0015 p {
  margin: 0 0 5px;
  opacity: 0;
  transition: opacity 0.35s, transform 0.35s;
}
#snip0015 h2 {
  word-spacing: -0.15em;
  font-weight: 300;
  text-transform: uppercase;
  transform: translate3d(0%, 50%, 0);
  transition-delay: 0.3s;
  margin-bottom: 40px;
}
#snip0015 h2 span {
  font-weight: 800;
}
#snip0015 p {
  font-weight: 200;
  transition-delay: 0s;
}
#snip0015 a {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
  color: #ffffff;
}
#snip0015:hover img {
  opacity: 0.35;
}
#snip0015:hover #figcaption h2 {
  opacity: 1;
  transform: translate3d(0%, 0%, 0);
  transition-delay: 0.3s;
}
#snip0015:hover #figcaption p {
  opacity: 0.9;
  transition-delay: 0.6s;
}
#snip0015:hover #figcaption::before {
  background: rgba(255, 255, 255, 0);
  top: 30px;
  bottom: 30px;
  opacity: 1;
  transition-delay: 0s;
}


